.spinner {
    display: flex;
    padding: 30px 0;
    justify-content: center;
    align-items: center;
}

.loader {
    width: calc(7em + 1vw);
    height: calc(7em + 1vw);
    border: 10px solid #f3f3f3;
    border-top: 10px solid #383636;
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
}

@keyframes spinner {
    0% {
        transform: rotate(0deg);
    }
    100%  {
        transform: rotate(360deg);
    }
}